HttpClient
@Injectable({
    providedIn: 'root'
})
export class GetdataService {

    constructor(private _http: HttpClient) { }
    /* private host = "https://api.coronastatistics.live" */
    private hostTimeLine = "https://pomber.github.io/covid19/timeseries.json"
   private host = "https://api.thevirustracker.com/free-api?countryTotals=ALL";
   /* private host = "assets/data.json"; */
    /* private host = "https://www.bing.com/covid/data" */

    getAll(type): Observable<Country>{
        /* return this._http.get<Country>(`${this.host}/countries?sort=${type}`).pipe( */
        return this._http.get<Country>(`${this.host}`).pipe(
            retry(1),
            catchError(this.handleError)
        );
    }
    getAllData(type): Observable<Country>{
        /* return this._http.get<Country>(`${this.host}/countries?sort=${type}`).pipe( */
        return this._http.get<Country>(`${this.host}`).pipe(
            retry(5),
            catchError(this.handleError)
        );
    }
    getCountry(name): Observable<Country>{
        return this._http.get<Country>(`${this.host}/countries/${name}`).pipe(
            retry(1),
            catchError(this.handleError)
        );
    }
    getTimeline(){
        /* return this._http.get(`${this.host}/timeline`).pipe( */
        return this._http.get(`${this.hostTimeLine}`).pipe(
            retry(1),
            catchError(this.handleError)
        );
    }
    getTimelineCountry(country){
        return this._http.get(`${this.host}/timeline/${country}`).pipe(
            retry(1),
            catchError(this.handleError)
        );
    }
    getTimelineGlobal(){
        /* return this._http.get(`${this.host}/timeline/global`).pipe( */
        /* const http$ = this._http.get(`${this.host}`); */
        const http$ = this._http.get(`${this.hostTimeLine}`);
        http$.subscribe(
            res => console.log('HTTP response', res),
                err => console.log('HTTP Error', err),
                () => console.log('HTTP request completed.')
        );
        return http$.pipe(
            retry(1),
            catchError(this.handleError)
            /* catchError(res=> {console.log(res);}) */
        );
    }

    handleError(error) {
        let errorMessage = '';
        if(error.error instanceof ErrorEvent) {
            // Get client-side error
            errorMessage = error.error.message;
        } else {
            // Get server-side error
            errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
        }
        window.alert("Please check your internet connection!.");
        return throwError(errorMessage);
    }
}
